import {AfterViewInit, Component, EventEmitter, Input, OnInit, Output} from '@angular/core';
import {CustomerApi} from '../../services/customer.api';
import {ActivatedRoute, Router} from '@angular/router';
import {Customer} from '../../../ic-core';
import {AlertDialog} from '../../../ic-core';
import {ErrorStateMatcher, MatDialog} from '@angular/material';
import {FormControl, Validators} from '@angular/forms';
import * as jQuery from 'jquery';


@Component({
  selector: 'ic-customer-profile',
  templateUrl: './customer-information.component.html',
  styleUrls: ['./customer-information.component.scss']
})
export class CustomerInformationComponent implements OnInit, AfterViewInit {

  // tslint:disable-next-line:variable-name
  private _editMode: boolean;

  @Input('editMode')
  set editMode(mode: boolean) {
    this._editMode = !!mode;
    if (this._editMode) {
      this.emailFormControl.enable();
      this.phoneFormControl.enable();
    } else {
      this.emailFormControl.disable();
      this.phoneFormControl.disable();
    }

  }

  get editMode(): boolean {
    return this._editMode;
  }

  @Input() customer: Customer;
  backupCustomer: Customer;
  // @Output() onDataUpdate = new EventEmitter<void>();

  private updateProperties;

  public genders = [{value: true, title: 'Male'}, {value: false, title: 'Female'}];

  public phoneFormControl: FormControl = new FormControl({value: '', disabled: !this.editMode}, [
    Validators.required,
    Validators.pattern('^( *)([0-9]{10})( *)+$')
  ]);

  public emailFormControl: FormControl = new FormControl({value: '', disabled: !this.editMode}, [
    // Validators.required,
    Validators.email
  ]);

  matcher = new ErrorStateMatcher();

  constructor(private customerApi: CustomerApi,
              private route: ActivatedRoute,
              private router: Router,
              private dialog: MatDialog) {
  }


  ngOnInit() {
  }

  ngAfterViewInit(): void {

  }


  private async create() {
    try {
      this.customer = await this.customerApi.create(this.customer);
      await this.router.navigate([`accounts/customers/${this.customer.id}`]);
    } catch (err) {
      const errorMessage = 'Create Failed';
      this.dialog.open(AlertDialog, {
        width: '300px',
        data: errorMessage
      });
    }
  }

  private async update() {
    this.updateProperties = {id: this.customer.id, ...this.updateProperties};
    try {
      await this.customerApi.update(this.updateProperties as Customer);
    } catch (err) {
      const errorMessage = 'Update Failed';
      this.dialog.open(AlertDialog, {
        width: '300px',
        data: errorMessage
      });
    }
  }


  public changeProperty(propertyName: string) {
    this.updateProperties = this.updateProperties || {};
    this.updateProperties[propertyName] = this.customer[propertyName];
  }

  public async toggleEdit() {
    if (this.editMode) {
      if (this.isValidInput()) {
        if (!!this.customer.id) {
          await this.update();
        } else {
          await this.create();
        }
        this.editMode = false;
      }
    } else {
      this.backupCustomer = new Customer(JSON.parse(JSON.stringify(this.customer)));
      this.editMode = true;
    }

  }

  public cancelEdit() {
    this.editMode = false;
    this.customer = this.backupCustomer;
    this.backupCustomer = undefined;
  }

  public isValidInput(): boolean {
    return !!this.customer.fullName &&
      !!this.customer.birthday &&
      !!this.customer.gender &&
      !!this.customer.contact.phones[0].number &&
      !!this.customer.contact.addresses[0].street &&
      !!this.customer.contact.addresses[0].district &&
      !!this.customer.contact.addresses[0].city &&
      !this.emailFormControl.invalid;
  }

}
