import {Component, OnInit, ViewChild} from '@angular/core';
import {AbstractPageComponent, ConfirmDialog, DialogService, UtilityService} from '../../../ic-core';
import {Address, Contact, Customer, Phone} from '../../../ic-core';

import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';
import {MatTableDataSource} from '@angular/material/table';
import {MatDialog} from '@angular/material';
import {CustomerApi} from '../../services/customer.api';
import * as moment from 'moment';
import {Router} from '@angular/router';

const title = 'Customers';
const breadcrumb = [{title: 'Account'}, {title: 'Customers'}];

function compare(a: number | string, b: number | string, isAsc: boolean) {
  return (a < b ? -1 : 1) * (isAsc ? 1 : -1);
}

@Component({
  selector: 'ic-customers',
  templateUrl: './customers.component.html',
  styleUrls: ['./customers.component.scss']
})
export class CustomersComponent extends AbstractPageComponent implements OnInit {

  public searchCondition = {
    searchString: '',
    periodFrom: new Date(),
    periodTo: new Date(),
    filterBy: 'TODAY_APPOINTMENTS'
  };

  displayedColumns: string[] = ['photo', 'id', 'fullName', 'contact.phones.0.number', 'contact.email', 'contact.addresses.0', 'actions'];
  dataSource: MatTableDataSource<Customer>;

  public customers: Customer[] = [];

  constructor(private customerApi: CustomerApi,
              public dialogService: DialogService,
              private utilityService: UtilityService,
              private router: Router) {
    super(title, breadcrumb);
  }

  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: true}) sort: MatSort;


  ngOnInit() {
    this.search();

  }

  private compileSearchObject() {
    return this.searchCondition; // this.searchCondition;
  }

  public validatePeriod() {
    if (this.searchCondition.periodFrom && this.searchCondition.periodTo) {
      if (moment(this.searchCondition.periodFrom).isAfter(moment(this.searchCondition.periodTo))) {
        const currentPeriodFrom = this.searchCondition.periodFrom;
        this.searchCondition.periodFrom = this.searchCondition.periodTo;
        this.searchCondition.periodTo = currentPeriodFrom;
      }
    }
  }

  public search() {
    const searchObject = this.compileSearchObject();
    this.customerApi.search(searchObject).subscribe(ressult => {
      this.customers = ressult;
      this.dataSource = new MatTableDataSource(this.customers);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sortingDataAccessor = (obj, property) => {
        let value: any = UtilityService.getDeeplyProperty(obj, property);
        if (property === 'contact.addresses.0') {
          const address: Address = value as Address;
          value = `${address.city} ${address.district} ${address.street}`;
        }
        return value;
      };
      this.dataSource.sort = this.sort;
    });
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  async deleteCustomer(customer: Customer) {
    const result = await this.dialogService.openConfirm(`Do you want to delete "${customer.fullName}" (id: ${customer.id})?`);
    if (result) {
      await this.customerApi.delete(customer.id);
      const index: number = this.dataSource.data.findIndex(item => item.id === customer.id);
      this.dataSource.data.splice(index, 1);
      this.dataSource._updateChangeSubscription();
      // this.dataSource = new MatTableDataSource<Customer>(this.dataSource.data);
    }
  }

  async gotoDetail(customer: Customer) {
    const routerLink = 'accounts/customers/' + customer.id;
    await this.router.navigate([routerLink]);
  }
}
