import {Injectable} from '@angular/core';
import {HttpClient} from '@angular/common/http';
import {Address, Contact, Customer, Phone} from '../../ic-core';
import {Observable, Subscriber} from 'rxjs';
import {CustomerRepository} from '../repositories/customer.repository';
import {AlertDialog, UtilityService} from '../../ic-core';
import {CustomerApiRequestAdapter} from '../adapters/customer-api-request.adapter';

@Injectable({
  providedIn: 'root'
})
export class CustomerApi {

  constructor(protected customerRepository: CustomerRepository,
              protected customerApiRequestAdapter: CustomerApiRequestAdapter,
              protected utilityService: UtilityService) {
  }

  public async create(customer: Customer): Promise<Customer> {
    // let customerID: CustomerID;
    try {
      customer = await this.customerApiRequestAdapter.create(customer);
      await this.customerRepository.post(customer);
    } catch (err) {
      throw err;
    }
    return customer;
  }


  public async update(dataChanged: Customer) {
    await this.customerApiRequestAdapter.update(dataChanged.id, dataChanged);
    await this.customerRepository.patch(dataChanged);
  }

  public async replace(customer: Customer) {
    await this.customerRepository.put(customer);
  }

  public async delete(id) {
    await this.customerApiRequestAdapter.delete(id);
    await this.customerRepository.delete(id);
  }

  public search(query: any): Observable<Customer[]> {
    // @ts-ignore
    const observable = new Observable<Customer[]>(async observer => {
      let result: Customer[] = await this.customerRepository.search({
        fullName: {
          $regex: '.*' + query.searchString + '.*'
        }
      });
      observer.next(result);
      result = await this.customerApiRequestAdapter.search({textSearch: query.searchString});
      observer.next(result);
    });
    return observable;
  }

  async get(customerId: string): Promise<Customer> {
    const customer: Customer = await this.customerApiRequestAdapter.get(customerId);
    return customer;
  }

  createEmptyCustomer() {
    const name = '';
    // @ts-ignore
    const address: Address = {};
    address.city = '';
    address.district = '';
    address.street = '';

    // @ts-ignore
    const phone: Phone = {
      number: '123456789',
      primary: true,
      type: Phone.TypeEnum.Mobile
    };
    // @ts-ignore
    const contact: Contact = {
      email: ``,
      addresses: [address],
      phones: [phone]
    };
    const customer: Customer = new Customer({
      fullName: name,
      birthday: new Date(),
      gender: true,
      photo: 'assets/images/xs/avatar.jpg',
      contact
    });
    return customer;
  }


}


