import { Injectable } from '@angular/core';
import {ApiRequestAdapter, Customer, InitializerProvider} from '../../ic-core/';
import {HttpClient} from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CustomerApiRequestAdapter extends ApiRequestAdapter<Customer> {
  protected uri: string;
  constructor(protected httpClient: HttpClient, initializerProvider: InitializerProvider) {
    super(Customer, httpClient, initializerProvider);
    this.uri = initializerProvider.CONFIG.api.uri.customers;
  }
}
