import { Injectable } from '@angular/core';
import {AbstractRepository} from '../../ic-core';
import {Address, Contact, Customer, Phone} from '../../ic-core';

@Injectable({
  providedIn: 'root'
})
export class CustomerRepository extends AbstractRepository<Customer> {
  constructor() {
    super(Customer);
  }


  private createDefaulData() {
    Array.from({length: 15}, (_, k) => {
      const customer = createNewCustomer(k + 1);
      this.post(customer).then(r => {});
    });
  }

  protected postInit() {
     this.createDefaulData();
  }
}

const NAMES: string[] = [
  'Maia', 'Asher', 'Olivia', 'Atticus', 'Amelia', 'Jack', 'Charlotte', 'Theodore', 'Isla', 'Oliver',
  'Isabella', 'Jasper', 'Cora', 'Levi', 'Violet', 'Arthur', 'Mia', 'Thomas', 'Elizabeth'
];

/** Builds and returns a new User. */
function createNewCustomer(id: number): Customer {
  const name = NAMES[Math.round(Math.random() * (NAMES.length - 1))] + ' ' +
    NAMES[Math.round(Math.random() * (NAMES.length - 1))].charAt(0) + '.';

  // @ts-ignore
  const address: Address = {};
  address.city = 'city city';
  address.district = 'district district ';
  address.street = 'street street' + id;
  // @ts-ignore
  const phone: Phone = {
    number: `(+84)${id}${id}${id}${id}${id}${id}${id}${id}${id}`.substr(0, 12),
    primary: true,
    type: Phone.TypeEnum.Mobile
  };

  // @ts-ignore
  const contact: Contact = {
    email: `email${id}@email.com`,
    addresses: [address],
    phones: [phone]
  };

  const customer: Customer = new Customer({
    fullName: name,
    photo: 'assets/images/xs/avatar.jpg',
    birthday: new Date(),
    gender: true,
    contact
  });
  customer.id = id.toString()
  return customer;
}
